import React, { useState } from 'react';
import Layout from '../components/Layout';

const Login = () => {
	const [isOpen, setIsOpen] = useState(false);
	const toggle = () => setIsOpen(!isOpen);

	return (
		<Layout>
			<section>
				<div className="container pt-3">
					<div className="row">
						<div className="col-md-4 offset-md-4">
							<div className="toggle-login">
								<div className="login">
									<h3 className="mb-4">Entrar</h3>
									
									<form method="post" id="loginform">
										<p className="login-username">
											<label htmlFor="user_login">Nome de usuário ou endereço de e-mail</label>
											<input type="text" name="log" id="user_login" className="input" size="20" />
										</p>

										<p className="login-password">
											<label htmlFor="user_pass">Senha</label>
											<input type="password" name="pwd" id="user_pass" className="input" size="20" />
										</p>
										
										<p className="login-remember">
											<label>
												<input name="rememberme" type="checkbox" id="rememberme" value="forever" /> Lembrar-me
											</label>
										</p>

										<p className="login-submit">
											<input type="submit" name="wp-submit" id="wp-submit" className="button button-primary" value="Acessar" />
										</p>
									</form>

									<hr className="ou my-4" />
									<div className="text-center">
										<a className="btn btn-lg btn-success show-register" onClick={toggle}>Criar nova conta</a>
										<a className="btn btn-lg btn-success show-register" onClick={toggle}>Criar nova conta</a>
									</div>
								</div>

								{isOpen ?? (<div className="register">
									<h3 className="mb-4">Criar conta</h3>
									
									<form method="post">
										<div className="form-group">
											<label>Usuário</label>
											<input type="text" name="user_login" id="user_login" className="form-control alt" />
										</div>

										<div className="form-group">
											<label>E-mail</label>
											<input type="text" name="user_email" id="user_email" className="form-control alt" />
										</div>

										<p className="text-muted">Uma confirmação de registro será enviada para você por e-mail.</p>
										<input className="btn btn-lg btn-block btn-success" type="submit" value="Cadastre-se" id="register" />

										<div className="text-center text-muted mt-4">
											Já tem uma conta? <a className="text-link text-primary show-login">Entre agora</a>
										</div>
									</form>
								</div>)}
							</div>
						</div>
					</div>
				</div>
			</section>
		</Layout>
	);
}

export default Login;